<script>
import Blockly from "./utils/blockly";
import Interpreter from "js-interpreter";
import * as ZH_HANS from "./utils/blockly/msg/zh-hans";
import "./utils/blockly/python";
import "./utils/blockly/javascript";
import "./utils/blockly/php";
import "./utils/blockly/dart";
import "./utils/blockly/lua";
Blockly.setLocale(ZH_HANS);

var workspace;

function customAlert(text, callback) {
  const div = document.createElement("div");
  div.style =
    "position:fixed;top:32px;left:10vw;width:80vw;background-color:#f0f0f0;border-radius:10px;padding:0 16px 16px;box-sizing:border-box;z-index:9999;box-shadow:0 0 4px rgb(0 0 0 / 14%), 0 4px 8px rgb(0 0 0 / 28%);";
  div.id = "ohos-value-prompt";
  div.innerHTML = `<p>${text}</p><div style="float:right"><button id="ohos-value-confirm" style="display: inline-flex;justify-content: center;align-items: center;line-height: 1;height: 32px;white-space: nowrap;cursor: pointer;-webkit-appearance: none;text-align: center;box-sizing: border-box;outline: none;font-weight: 500;user-select: none;vertical-align: middle;padding: 8px 15px;font-size: 14px;border-radius: 4px;background-color:#409eff;border:1px solid #409eff;color:#fff;margin-right:12px;"><span></span></button></div>`;
  document.querySelector("body").appendChild(div);
  document
    .getElementById("ohos-value-confirm")
    .addEventListener("click", function () {
      document.getElementById("ohos-value-prompt").remove();
      callback(undefined);
    });
}

function customPrompt(text, callback) {
  const div = document.createElement("div");
  div.style =
    "position:fixed;top:32px;left:10vw;width:80vw;background-color:#f0f0f0;border-radius:10px;padding:0 16px 16px;box-sizing:border-box;z-index:9999;box-shadow:0 0 4px rgb(0 0 0 / 14%), 0 4px 8px rgb(0 0 0 / 28%);";
  div.id = "ohos-value-prompt";
  div.innerHTML = `<p>${text}</p><input id="ohos-value-input" style="width: 100%;-webkit-appearance: none;color: #606266;background-color:#ffffff;font-size: inherit;height: 32px;line-height: 32px;padding: 0 11px;outline: none;border:none;border-raduis: 4px;box-shadow:0 0 0 1px #dcdfe6 inset;box-sizing: border-box;margin-bottom:12px;" /><div style="float:right"><button id="ohos-value-confirm" style="display: inline-flex;justify-content: center;align-items: center;line-height: 1;height: 32px;white-space: nowrap;cursor: pointer;-webkit-appearance: none;text-align: center;box-sizing: border-box;outline: none;font-weight: 500;user-select: none;vertical-align: middle;padding: 8px 15px;font-size: 14px;border-radius: 4px;background-color:#409eff;border:1px solid #409eff;color:#fff;margin-right:12px;"><span></span></button><button id="ohos-value-cancel" style="display: inline-flex;justify-content: center;align-items: center;line-height: 1;height: 32px;white-space: nowrap;cursor: pointer;-webkit-appearance: none;text-align: center;box-sizing: border-box;outline: none;font-weight: 500;user-select: none;vertical-align: middle;padding: 8px 15px;font-size: 14px;border-radius: 4px;background-color:#909399;border:1px solid #909399;color:#fff"></button></div>`;
  document.querySelector("body").appendChild(div);
  document
    .getElementById("ohos-value-confirm")
    .addEventListener("click", function () {
      const value = document.getElementById("ohos-value-input").value;
      document.getElementById("ohos-value-prompt").remove();
      callback(value);
    });
  document
    .getElementById("ohos-value-cancel")
    .addEventListener("click", function () {
      const value = document.getElementById("ohos-value-input").value;
      document.getElementById("ohos-value-prompt").remove();
      callback(value);
    });
}

export default {
  name: "App",
  mounted() {
    workspace = Blockly.inject("primaryDiv", {
      toolbox: document.getElementById("toolbox"),
    });
    workspace.setTheme(Blockly.Themes.Zelos);
    const xml = localStorage.getItem("content-save");
    if (xml) {
      Blockly.Xml.domToWorkspace(Blockly.Xml.textToDom(xml), workspace);
    } else {
      const defaultBlocks = document.getElementById("blocklyDefault");
      Blockly.Xml.domToWorkspace(defaultBlocks, workspace);
    }
    workspace.addChangeListener(this.updateFunction);
    window.getCode = this.getCode;
    window.resetWorkspace = this.resetWorkspace;
    window.runCode = this.runCode;
  },
  methods: {
    updateFunction() {
      const text = Blockly.Xml.domToText(Blockly.Xml.workspaceToDom(workspace));
      localStorage.setItem("content-save", text);
    },
    getCode(language) {
      return Blockly[language || "JavaScript"].workspaceToCode(workspace);
    },
    resetWorkspace() {
      const defaultBlocks = document.getElementById("blocklyDefault");
      if (Blockly.Xml.clearWorkspaceAndLoadFromXml(defaultBlocks, workspace)) {
        return "success";
      } else {
        return "fail";
      }
    },
    runCode() {
      var code = Blockly.JavaScript.workspaceToCode(workspace);
      var initFunc = function (interpreter, scope) {
        var alertWrapper = function (text, callback) {
          text = text ? text.toString() : "";
          return customAlert(text, callback);
        };
        interpreter.setProperty(
          scope,
          "alert",
          interpreter.createAsyncFunction(alertWrapper)
        );
        var promptWrapper = function (text, callback) {
          text = text ? text.toString() : "";
          return customPrompt(text, callback);
        };
        interpreter.setProperty(
          scope,
          "prompt",
          interpreter.createAsyncFunction(promptWrapper)
        );
      };
      var myInterpreter = new Interpreter(code, initFunc);
      // eslint-disable-next-line no-unused-vars
      const codeRunner = function () {
        if (myInterpreter) {
          var hasMore = myInterpreter.run();
          if (hasMore) {
            setTimeout(codeRunner, 10);
          } else {
            console.log("finish");
          }
        }
      };
      codeRunner();
    },
  },
};
</script>

